@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap");
@import "./varibles.scss";
@import "./breakpoints.scss";
@import "./components/header.scss";
@import "./components/banner.scss";
@import "./components/cases.scss";
@import "./components/TextScroller.scss";

body,
html {
  user-select: none;
  background-color: $white;
  overscroll-behavior: none;
  margin: 0;
  font-family: "Neue Haas Grotesk Display Pro 35", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;  
  min-width: 100%;
  margin: 0 auto;
  height: 100%;
  
   /* @include media("<=1840px") {
     width: 1280px;
     min-width: 1280px;
   }
   @include media("<=desktop") {
     width: 1080px;
     min-width: 1080px;
   }
   @include media("<=1080px") {
    width: 100%;
     min-width: 100%;
   } */
  .row {
    display: flex;
    height: 100%;
    padding: 0 32px;
    /* @include media("<=phone") {
      padding: 0 16px;
    } */
  }
  .v-center {
    align-items: center;
  }
  .space-between {
    justify-content: space-between;
  }
}

.container-fluid {
  width: 100%;
  height: 100%;
  .row {
    display: flex;
    height: 100%;
  }
}
